let importAll = (requireContext: __WebpackModuleApi.RequireContext) => {
  return requireContext.keys().forEach(requireContext);
};
try {
  importAll(require.context("../assets/icons", true, /\.svg$/));
} catch (error) {
  console.log(error);
}
export default {
  props: ["name"],
   name: "Icon"
};
